import React, { useEffect, useRef, ReactDOM } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import TypeIt from "typeit-react";

const Home = () => {
  gsap.registerPlugin(ScrollTrigger);
  const ref = useRef(null);

  useEffect(() => {
    const element = ref.current;
    gsap.fromTo(
      element.querySelector("#img"),
      {
        scale: 1.3,
      },
      {
        scale: 0.7,
        duration: 2.5,
        ease: "linear",
        scrollTrigger: {
          trigger: element.querySelector(".mainImg"),
          start: "top center",
          end: "bottom top",
          scrub: true
        }
      }
    );
  }, []);

  useEffect(() => {
    const element = ref.current;
    gsap.fromTo(
      element.querySelector(".b1"),
      {
        x: -800,
      },
      {
        x: 0,
        duration: 2,
        delay: 1,
        ease: "linear",
        scrollTrigger: {
          trigger: element.querySelector("#Art span"),
          scrub: true
        }
      }
    );
  }, []);

  useEffect(() => {
    const element = ref.current;
    gsap.fromTo(
      element.querySelector(".b4"),
      {
        x: 1000
      },
      {
        x: 0,
        duration: 1,
        ease: "linear",
        scrollTrigger: {
          trigger: element.querySelector(".ArtImg"),
          scrub: true
        }
      }
    );
  }, []);







  return (
    <div ref={ref}>
      <div className="mainText">
        <p>
          <TypeIt
            options={{
              strings: ["Grimpan exists for those who cherish their daily lives, dream of a perfect day, and are willing to enjoy art fordevelopment.Where everyone's inspiration for accepting changes and trends in art culture and responding flexibly becomes a reality.It's called Grimpan."],
              speed: 30,
              loop: true,
              waitUntilVisible: false,
            }} />
        </p>
      </div>

      <div className="mainImg">
        <figure >
          <img src="/grimpan/img/main.jpg" alt="main" id="img" />
        </figure>
      </div>
      <section id="About">
        <h2>About</h2>
        <div className="AboutText">
          <p>Grimpan은  2021년 8월에 론칭하여 65명 국내의 문화 예술계 창작자들을 소개하고 그들의 작품을 판매하고 있으며, 온라인 매장과 더불어 전시형 팝업스토어를 함께 운영하고 있습니다.</p>
          <p>여러 장르의 예술품을 새롭게 선보이며 이제 전시회나 특별한 날에만 예술을 접하는 것이 아닌 일상에서도 즐길 수 있도록 한걸음 나아가고 있습니다. 여러분의 예술적인 라이프스타일을 위해 Grimpan는 여러분의 일상 곳곳에 예술상품으로 영감을 전달합니다.</p>
          <p>Launched in August 2021, it introduces 65 domestic cultural and artistic creat ors and sells their works, and operates an exhibition-type pop-up store along with an online store.</p>
          <p>Now taking a step forward so that you can enjoy art in your daily life, not just on exhibitions or special occasions. For your artistic lifestyle, Grimpan delivers inspiration throughout your daily life with art products.</p>
        </div>
      </section>
      <section id="Art">
        <h2>the art of life</h2>
        <p>
          <span>
            이상적이고 예술적인 가치를 일상속에서도 녹아들 수 있게 다양한 형태로 작업합니다.
          </span>
          <span>
            여러형태의 이미지들은 구매하다로 끝나는게 아니라 작업물을 소장하다의 의미로 다가와주셨으면 합니다. 이미지 저작권, 디자인 저작권은 Grimpan에게 있습니다.
          </span>
        </p>
        <div className="ArtImg">
          <figure>
            <img src="/grimpan/img/Artimg.jpg" alt="artimg" />
          </figure>
        </div>
        <div className="artLife">
          <h2>Life</h2>
          <div className="artBox">
            <div className="box b1">
              <figure>
                <img src="/grimpan/img/arts1.jpg" alt="arts1" />
              </figure>
            </div>
            <div className="box b2">
              <div className="front">
                <figure>
                  <img src="/grimpan/img/artbig1.jpg" alt="artbig1" />
                </figure>
              </div>
              <div className="back">
                <span>Life</span>
              </div>
            </div>
            <div className="box b3">
              <div className="front">
                <figure>
                  <img src="/grimpan/img/artbig2.jpg" alt="artbig2" />
                </figure>
              </div>
              <div className="back">
                Life
              </div>
            </div>
            <div className="box b4">
              <figure>
                <img src="/grimpan/img/arts2.jpg" alt="arts2" />
              </figure>
            </div>
          </div>
        </div>
      </section>
      <section id="Product">
        <div className="Artist">
          <div className="artist_left">
            <div className="artist_text">
              <h2>Artist</h2>
              <p>
                Grimpan 소속 작가들의 소개, 다양한 종류의 작품들과 디자인, 해석을 알 수 있습니다.
              </p>
            </div>
            <button type="button">VIEW MORE</button>
          </div>
          <div className="artist_right">
            <figure>
              <img src="/grimpan/img/pro1.jpg" alt="pro1" />
            </figure>
          </div>
        </div>
        <div className="Product">
          <div className="product_left">
            <figure>
              <img src="/grimpan/img/pro2.jpg" alt="pro2" />
            </figure>
          </div>
          <div className="product_right">
            <div className="product_text">
              <h2>Product</h2>
              <p>Grimpan 소속 아티스트트들의 작품을 재해석한 예술상품들을 볼 수 있습니다.</p>
            </div>
            <button type="button">VIEW MORE</button>
          </div>
        </div>
      </section>
      <section id="popup">
        <h2>PopUp</h2>
        <div className="swiperB">
          <Swiper
            slidesPerView={1}
            spaceBetween={0}
            pagination={{
              clickable: true,
            }}
            modules={[]}
            breakpoints={{
              450: {
                slidesPerView: 2,
                spaceBetween: 30,
              },
              850: {
                slidesPerView: 3,
                spaceBetween: 30,
              }

            }}
            className="mySwiper"
          >
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop1.jpg" alt="pop1" />
                </figure>
                <p>더현대 서울</p>
                <span>03/30 - 5/10</span>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop2.jpg" alt="pop2" />
                </figure>
                <p>영등포 타임스퀘어</p>
                <span>05/15 - 06/30</span>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop3.jpg" alt="pop3" />
                </figure>
                <p>더현대 대구 오픈예정</p>
                <span>07/05 - 08/20</span>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop4.jpg" alt="pop4" />
                </figure>
                <p>현대백화점 부산 오픈예정</p>
                <span>08/30 -1 0/01</span>
              </div>
            </SwiperSlide>
          </Swiper>

        </div>
      </section>
      <section id="videos">
        <h2>Videos</h2>
        <div className="video_content">
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v1.jpg" alt="v1" />
              <span></span>
            </figure>
            <h3>더현대 서울  팝업</h3>
            <p>Live painting  박효빈</p>

          </div>
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v2.jpg" alt="v2" />
              <span></span>
            </figure>
            <h3>맥시멀한 나만의 작업실</h3>
            <p>Grimpan크리에이터의 작업실 소개</p>

          </div>
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v3.jpg" alt="v3" />
              <span></span>
            </figure>
            <h3>Grimpan 소속작가들</h3>
            <p>소속작가들의 전시 둘러보기</p>

          </div>
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v4.jpg" alt="v4" />
              <span></span>
            </figure>
            <h3>나만의 공간 꾸미기</h3>
            <p>Grimpan 소속 크리에이터들의 공간 꾸미기</p>

          </div>
        </div>
      </section>
    </div>
  )
}



export default Home;