import logo from './logo.svg';
import './App.css';
import './About.css';
import './Works.css';
import { Routes, Route, Link } from 'react-router-dom';
import About from './About';
import Works from './Works';
import Home from './Home';
import { useEffect, useState, useRef } from 'react';

function App() {
  const [showsbtn, setshowsbtn] = useState(false);

  const gotop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    })
  }

  useEffect(() => {
    const hidebtn = () => {
      if (window.scrollY > 500) {
        setshowsbtn(true)
      } else {
        setshowsbtn(false)
      }
    }
    window.addEventListener("scroll", hidebtn)
    return () => {
      window.removeEventListener("scroll", hidebtn)
    }
  });


  const [scrollPosition, setScrollPosition] = useState(0);
  const updateScroll = () => {
    setScrollPosition(window.scrollY || document.documentElement.scrollTop);
  }
  useEffect(() => {
    window.addEventListener('scroll', updateScroll);
  });


  return (
    <div id='wrap'>
      <header className={scrollPosition < 100 ? "headerC" : "headerF"}>
        <div className='headerI'>
          <h1><Link to={"/grimpan"} className='tt'>Grimpan</Link></h1>
          <ul>
            <li><Link to={"/About"}>About</Link></li>
            <li><Link to={"/Works"}>Artist</Link></li>
          </ul>
        </div>
      </header>
      <Routes>
        <Route path="/grimpan" exact={true} element={<Home />}></Route>
        <Route path="/About" element={<About />}></Route>
        <Route path="/Works" element={<Works />}></Route>
      </Routes>
      <footer>
        <div className="f_inner">
          <h2>Grimpan</h2>
          <div className="copy">
            <div className="f_nav">
              <ul>
                <li><a href="#">대구 중구 동성로1길 15 5층</a></li>
                <li><a href="#">010-0000-0000</a></li>
                <li><a href="#">kc06056@naver.com</a></li>
                <li><a href="#">ⓒ김태완</a></li>
                <li><a href="#">ⓒ박효빈</a></li>
              </ul>
            </div>
            <div className="sns">
              <ul>
                <li><a href="#">instagram</a></li>
                <li><a href="#">facebook</a></li>
                <li><a href="#">youtube</a></li>
              </ul>
            </div>
          </div>
        </div>
      </footer>
      {
        showsbtn &&
        <button type='button' className='Topbtn' onClick={gotop}>Top</button>
      }

    </div>
  );
}

export default App;
